.news__nav {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    background: #fafafa;
    -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
            box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
    z-index: 512;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}

.news__nav__link {
    /* width: 50px; */
    text-align: center;
    padding: 1.618em 0 0.618em;
    cursor: pointer;
}

.nav__selected {
    background: #fafafa;
    font-weight: 500;
    position: relative;
    z-index: 3;
}

.nav__selected::after {
    content: "";
    display: block;
    width: 50px;
    height: 2px;
    background: black;
    margin: 0.33em auto 0.33em;
}

@media (max-width: 450px) {
    .news__nav {
        overflow: hidden;
    }
    /* .news__nav .news__nav__link:not(.nav__selected) {
    } */
}


