body {
    font-family: 'Avenir'
}

.main-wrapper,
.app-wrapper {
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.app-wrapper {
    -webkit-animation: 1s fadeIn ease-out;
            animation: 1s fadeIn ease-out;
}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }
}

@keyframes fadeIn {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }
}


